html {
  scroll-behavior: smooth;
}

@font-face {
  font-family: "vanio-light";
  src: url("./assets/fonts/vanio.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "vanio";
  src: url("./assets/fonts/vanio.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "vanio-bold";
  src: url("./assets/fonts/vanio.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .d {
    @apply border border-rose-800;
  }


}

.bg-gradient {
  background: linear-gradient(180deg, #FFECCB -30.84%, rgba(255, 236, 203, 0) 141%);
}

.bg-green {
  background-color: #2e9fa3;
}
